.menu-surface {
  position: relative;
}
.menu-surface__header {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.menu-surface__body {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 10;
  transform: translate(0%, 105%);
}
.menu-surface--open .menu-surface__arrow {
  padding-right: 0.7em;
  transform: rotate(180deg);
}
.menu-surface--closed .menu-surface__arrow {
  padding-left: 0.7em;
}
.menu-surface--open .menu-surface__body {
  display: block;
}
.menu-surface--closed .menu-surface__body {
  display: none;
}
