.section_navbar-middle {
  background: #ffffff;
}
.section_navbar-middle .section__links {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}
.section_navbar-middle .section__links--full {
  padding-top: 2.3rem;
}
@media (max-width: 413px) {
  .section_navbar-middle .section__links--full {
    display: none;
  }
}
.section_navbar-middle .section__links--shrink {
  justify-content: flex-end;
}
@media (min-width: 414px) {
  .section_navbar-middle .section__links--shrink {
    display: none;
  }
}
.section_navbar-middle .section__link {
  display: flex;
  align-items: center;
  text-decoration: none;
}
@media (min-width: 414px) {
  .section_navbar-middle .section__link {
    margin-right: 4.5em;
    margin-bottom: 2.3em;
  }
}
@media (max-width: 413px) {
  .section_navbar-middle .section__link {
    padding: 2.25em;
  }
}
@media (min-width: 414px) {
  .section_navbar-middle .section__link--inactive {
    opacity: 0.5;
  }
}
@media (max-width: 413px) {
  .section_navbar-middle .section__link--inactive {
    opacity: 0.8;
  }
}
.section_navbar-middle .section__link--inactive:hover {
  opacity: 1;
}
.section_navbar-middle .section__link-text {
  padding-left: 1.5rem;
  font-size: 1.6em;
  line-height: 1.2;
  text-transform: uppercase;
  color: #1d1d1d;
}
.section_navbar-middle .section__links--shrink .menu-surface__arrow path {
  fill: #000;
}
.section_navbar-middle .section__links--shrink .menu-surface__body {
  background: #fafafa;
  border-radius: 12px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
}
.section_navbar-middle .section__links--shrink .section__link--inactive {
  opacity: 0.8;
}
