/* http://meyerweb.com/eric/tools/css/reset/
   v4.0 | 20180602
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
main,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section {
  display: block;
}
/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
.button {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 90vw;
  box-sizing: border-box;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  white-space: nowrap;
  transition: box-shadow 0.1s, border 0.1s, background 0.1s, color 0.1s;
}
.section {
  display: flex;
  justify-content: center;
  padding-left: 2rem;
  padding-right: 2rem;
}
.text-wrap {
  display: inline-block;
}
.text-wrap span {
  display: inline-block;
  white-space: nowrap;
}
.wrapper_section-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 1362px;
  box-sizing: border-box;
}
@media (max-width: 767px) {
  .wrapper_section-content {
    max-width: 90vw;
  }
}
.table_theme_simple {
  padding: 2.5em 2em;
  background: #ffffff;
  border-radius: 20px;
  box-shadow: 0 22px 27px rgba(0, 0, 0, 0.1);
}
@media (max-width: 991px) {
  .table_theme_simple .table__table--desktop {
    display: none;
  }
}
@media (min-width: 992px) {
  .table_theme_simple .table__table--mobile {
    display: none;
  }
}
.table_theme_simple .table__title {
  margin-bottom: 0.4rem;
  padding-left: 1rem;
  font-size: 2.2em;
  line-height: 1.2;
}
.table_theme_simple .table__cell {
  padding: 2.1rem 1rem;
  text-align: left;
}
.table_theme_simple .table__cell-item {
  display: block;
}
.table_theme_simple .table__cell-item:not(:last-child) {
  margin-bottom: 0.5em;
}
.table_theme_simple .table__cell:last-child {
  text-align: right;
}
.table_theme_simple .table__table {
  width: 100%;
}
.table_theme_simple .table__row {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.table_theme_simple .table__row:nth-child(2n + 1) {
  background: rgba(196, 196, 196, 0.05);
}
.table_theme_simple .table__row_del_mob {
  width: 100%;
  display: inline-block;
}
.table_theme_simple .table__header .table__cell {
  display: table-cell;
  font-weight: 600;
  line-height: 1.2;
}
@media (min-width: 992px) {
  .table_theme_simple .table__header .table__cell {
    font-size: 1.7em;
  }
}
@media (max-width: 991px) {
  .table_theme_simple .table__header .table__cell {
    vertical-align: middle;
    font-size: 1.4em;
  }
}
.table_theme_simple .table__body .table__cell {
  font-weight: 500;
  font-size: 1.3em;
  line-height: 1.7;
  letter-spacing: 0.02rem;
}
.table_theme_simple .table__body .table__cell:not(.table__link) {
  color: #555555;
}
.table_theme_simple .table__link {
  color: #6978ff;
}
.table_theme_simple .table__more {
  display: block;
  margin-top: 2.6rem;
  text-align: right;
  font-size: 1.4em;
  color: #6978ff;
  text-decoration: none;
}
.link_theme_none {
  color: inherit;
  text-decoration: inherit;
}
[tooltip]:before {
  content: attr(tooltip);
  position: absolute;
  bottom: 0;
  left: 50%;
  padding: 1em;
  font-size: 1.3em;
  min-width: 8em;
  text-align: center;
  color: #fff;
  visibility: hidden;
  border-radius: 6px;
  background-color: rgba(0, 0, 0, 0.8);
  transform: translate(-50%, 100%);
}
[tooltip]:hover:before {
  visibility: visible;
}
html {
  font-size: 12px;
  background: #e5e5e5;
  font-weight: 500;
}
@media (max-width: 1199px) {
  html {
    font-size: 10px;
  }
}
body {
  font-family: sans-serif;
  font-weight: 400;
}
button {
  all: unset;
}
.section_navbar-middle .section__links--shrink {
  justify-content: flex-start !important;
}
