.pagination {
  max-width: 1362px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
  margin-top: 20px;
  display: flex;
  align-items: center;
}
.pagination .area {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 3rem;
  padding-left: 5px;
  padding-right: 5px;
  height: 3rem;
  font-weight: 500;
  font-size: 1.3em;
  line-height: 1.6;
  text-align: center;
  letter-spacing: 0.02em;
  color: #555555;
  border: 1px solid #cdcdcd;
  box-sizing: border-box;
  border-radius: 2px;
  cursor: pointer;
}
.pagination .active-area {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 3rem;
  padding-left: 5px;
  padding-right: 5px;
  height: 3rem;
  font-weight: 500;
  font-size: 1.3em;
  line-height: 1.6;
  text-align: center;
  letter-spacing: 0.02em;
  color: #555555;
  border: 1px solid #cdcdcd;
  box-sizing: border-box;
  border-radius: 2px;
  cursor: pointer;
  border: none;
  background: #1d1d1d;
  color: #ffffff;
}
.pagination__previous {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 3rem;
  padding-left: 5px;
  padding-right: 5px;
  height: 3rem;
  font-weight: 500;
  font-size: 1.3em;
  line-height: 1.6;
  text-align: center;
  letter-spacing: 0.02em;
  color: #555555;
  border: 1px solid #cdcdcd;
  box-sizing: border-box;
  border-radius: 2px;
  cursor: pointer;
  margin-right: 1em;
}
.pagination__previous--inactive {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 3rem;
  padding-left: 5px;
  padding-right: 5px;
  height: 3rem;
  font-weight: 500;
  font-size: 1.3em;
  line-height: 1.6;
  text-align: center;
  letter-spacing: 0.02em;
  color: #555555;
  border: 1px solid #cdcdcd;
  box-sizing: border-box;
  border-radius: 2px;
  cursor: pointer;
  margin-right: 1em;
  pointer-events: none;
}
.pagination__next {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 3rem;
  padding-left: 5px;
  padding-right: 5px;
  height: 3rem;
  font-weight: 500;
  font-size: 1.3em;
  line-height: 1.6;
  text-align: center;
  letter-spacing: 0.02em;
  color: #555555;
  border: 1px solid #cdcdcd;
  box-sizing: border-box;
  border-radius: 2px;
  cursor: pointer;
  margin-left: 1em;
}
.pagination__next--inactive {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 3rem;
  padding-left: 5px;
  padding-right: 5px;
  height: 3rem;
  font-weight: 500;
  font-size: 1.3em;
  line-height: 1.6;
  text-align: center;
  letter-spacing: 0.02em;
  color: #555555;
  border: 1px solid #cdcdcd;
  box-sizing: border-box;
  border-radius: 2px;
  cursor: pointer;
  margin-left: 1em;
  pointer-events: none;
}
.pagination__arrow-icon {
  max-width: 100%;
}
.pagination__pages {
  display: flex;
  align-items: center;
  margin: 0 auto;
}
.pagination__page {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 3rem;
  padding-left: 5px;
  padding-right: 5px;
  height: 3rem;
  font-weight: 500;
  font-size: 1.3em;
  line-height: 1.6;
  text-align: center;
  letter-spacing: 0.02em;
  color: #555555;
  border: 1px solid #cdcdcd;
  box-sizing: border-box;
  border-radius: 2px;
  cursor: pointer;
  margin: 0 0.1rem;
}
.pagination__page--active {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 3rem;
  padding-left: 5px;
  padding-right: 5px;
  height: 3rem;
  font-weight: 500;
  font-size: 1.3em;
  line-height: 1.6;
  text-align: center;
  letter-spacing: 0.02em;
  color: #555555;
  border: 1px solid #cdcdcd;
  box-sizing: border-box;
  border-radius: 2px;
  cursor: pointer;
  border: none;
  background: #1d1d1d;
  color: #ffffff;
  margin: 0 0.1rem;
}
.pagination__pages-separator {
  margin: 0 0.8rem;
  height: 2rem;
  font-weight: 500;
  font-size: 1.3em;
  line-height: 1.6;
  text-align: center;
  letter-spacing: 0.02em;
  color: #555555;
}
.pagination .pagination__arrow-icon {
  margin: 0 5px;
}
.pagination__page,
.pagination__page--active {
  width: auto;
  padding: 10px;
}
